import { toast } from 'react-toastify';
/** 
 * 0.       react-toastify    . 
 *   https://fkhadra.github.io/react-toastify/introduction
 * 1.   .
 * 2.           ,   ToastType:
    const showToast = () => {
    CustomToast(ToastType.SUCCESS, 'Pet added successfully! ');
  };
 * 3.         CustomToast  ToastType.
 * 4. 
 * 5.       CustomToast  ToastType   ctrl(cmd)+. 
 *     .  ,    . 
 * 6.  ,    ,  showToast().
 * 7. .
 * 8.    ,      .
 * examples
CustomToast(ToastType.ERROR, 'An error occurred! ');
CustomToast(ToastType.WARNING, 'This is a warning! ');
CustomToast(ToastType.INFO, 'This is some information ');
 */

export const ToastType = {
  SUCCESS: 'success',
  ERROR: 'error',
  WARNING: 'warning',
  INFO: 'info',
};

export const CustomToast = (type, message, options = {}) => {
  switch (type) {
    case ToastType.SUCCESS:
      toast.success(message, {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 2000,
        ...options,
      });
      break;
    case ToastType.ERROR:
      toast.error(message, {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 2000,
        ...options,
      });
      break;
    case ToastType.WARNING:
      toast.warn(message, {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 2000,
        ...options,
      });
      break;
    case ToastType.INFO:
      toast.info(message, {
        position: toast.POSITION.TOP_RIGHT,
        autoClose: 2000,
        ...options,
      });
      break;
    default:
      throw new Error('Call exorcist! ');
  }
};
